<template>
	<view>
		<wybar @clickLeft="back" :leftText="className"></wybar>
		<view class="row topSearch">
			<image src="../../../../static/search.png" mode="aspectFit"
				style="margin-left: 10px;width: 20px;height: 20px;"></image>
			<input class="sousuokuang" :placeholder="i18n.index.search" type="text" confirm-type="search"
				@confirm="doSearch">
		</view>
		<view class="row"
			style="width: 100%;height: 44px;background-color: #FFF;justify-content: space-around;align-items: center;margin-top: 10px;">
			<view class="row" @click="changeType(0)" style="align-items: center;">
				全部商圈
				<image :src="isShowClass == 0 ? '../../../../static/rank/rank-shang.png' : '../../../../static/rank/rank-xia.png'"
					mode="aspectFit" style="width: 15px;height: 15px;margin-left: 5px;">
				</image>
			</view>
			<view class="row" @click="juliyouxian()">
				距离优先
			</view>
			<view class="row" @click="haopingyouxian()">
				好评优先
			</view>
		</view>
		<scroll-view scroll-y="true" style="width: 100%;" :style="'height:' + scrollHeigth + 'px;'">
			<view v-for="(item,index) in listArray" :key="index" @click="gotoCell(item)" class="row item_back">
				<image mode="aspectFill" class="item_image"></image>
				<view class="column" style="margin-left: 8px;overflow: hidden;height: 72px;flex: 1;">
					<view class="item_biaoti">鼎泰丰包子铺noble店</view>
					<view class="row" style="height: 28px;align-items: center;">
						<image src="../../../../static/meishi/pingfen.png" mode="" style="width: 10px;height: 10px;"></image>
						<text style="font-size: 12px;font-weight: bold;color: #FFBF47;margin-left: 3px;">4.8</text>

						<image src="../../../../static/rank/dianping.png" mode=""
							style="width: 10px;height: 10px;margin-left: 8px">
						</image>
						<text style="font-size: 11px;color: #6f6f6f;margin-left: 3px;">点评 179</text>
						<image src="../../../../static/rank/renjun.png" mode=""
							style="width: 10px;height: 10px;margin-left: 8px">
						</image>
						<text style="font-size: 11px;color: #6f6f6f;margin-left: 3px;">人均 79</text>
					</view>

					<view class="item_content item_content_word">暹罗广场 韩餐</view>

				</view>
				<view style="color: #6f6f6f;font-size: 11px;margin-right: 10px;">
					11km
				</view>
			</view>
		</scroll-view>
		<view class="classView" v-if="isShowClass" :style="'top:' + (systemTop + 140) + 'px;'" @click="doHideClassView()">
			<scroll-view scroll-y="true"
				style="width: 100%;min-height: 200px;max-height: 300px;background-color: #FFF;">
				<view v-for="(item,index) in 15" :key="index" class="classItem"
					@click.stop="classItemClick(item,index)">
					全部分类
				</view>

			</scroll-view>
		</view>
	</view>
</template>

<script>
	import wybar from '@/components/wybar/wyBar.vue'

	export default {
		components: {
			wybar,
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		data() {
			return {
				listArray: [1,1,1,1,1,1,1,1,1,11,1,1,1,1,1,1,1,1,1],
				scrollHeigth: 0,
				classID: '',
				className: '',
				isShowClass: false,
				systemTop:44
			}
		},
		onReady() {
			const res = uni.getSystemInfoSync();
			var top = 44
			if (parseInt(res.safeArea.top) > top) {
				top = parseInt(res.safeArea.top)
				this.systemTop = top
			}
			this.scrollHeigth = getApp().globalData.windowHeight - top - 40 - 60-42
		},
		onLoad(option) {
			this.className = option.name
		},
		methods: {
			back() {
				uni.navigateBack({})
			},
			changeType(index) {
				this.isShowClass = true
			},
			classItemClick(item,index){
				this.isShowClass = false
			},
			doHideClassView(){
				this.isShowClass = false
				console.log('doHideClassView')
			},
		}
	}
</script>

